export default {
	data() {
		return {
			detail:[],
			date:''
		}
	},
	onLoad(e) {
		var t=this;	
		var workplan_id=e.id;
		//获取详情
		t.urlRequest.urlRequest(t.func.getOaWorkplanDetail,{workplan_id:workplan_id},function(res){
			t.detail=res.data.workplan_row
		})
	},
	methods: {
	}
}
